export class WedEditorBox extends WedEditorBase implements IWedEditorBoxSel, IWedEditorPreviewBar, IWedEditorOutlineBar, IWedEditorCommonBar, IWedEditorMetaBar, ILastDatasBuilder {

	config: OWedEditorBoxConfig;

	schema: Schema;

	barLayout: IWedEditorBarLayout;

	// @ts-ignore
	get scrollContainer() {return (this.shadowRoot.getElementById("scrollBox")) as HTMLElement}

	protected _loadings: Promise<any>[];
	protected _inSync: Promise<any>;

	protected initUi(init: OWedEditorBoxConfig) {
		this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.barLayout = init.barLayout || new FullLayout();
		this.barLayout.initLayout(this, this.reg);
		if (!init.disableDefaultAccelKeys) {
			if (init.customAccelKeysList) {
				this.wedMgr.accelKeyMgr.initFromMapActions(this.reg.mergeListsAsMap<Action<IWedEditor>>(init.customAccelKeysList, "accelKeys:wed:global"));
			} else {
				this.wedMgr.accelKeyMgr.initFromMapActions(this.reg.getListAsMap<Action<IWedEditor>>("accelKeys:wed:global"));
			}
		} else if (init.customAccelKeysList) {
			this.wedMgr.accelKeyMgr.initFromMapActions(this.reg.getListAsMap<Action<IWedEditor>>(init.customAccelKeysList));
		}
	}

	/**
	 * Permet de dÃ©clarer une librairie de composants qui se charge en asynchrone.
	 * L'Ã©diteur ne sera effectivement construit que lorsque ces librairies seront chargÃ©es.
	 */
	addAsyncLib(lib: Promise<any>): Promise<any> {
		if (this._loadings == null) {
			this._loadings = [];
			this._inSync = null;
		}
		this._loadings.push(lib);
		return lib;
	}

	/** Promise rÃ©solue lorsque les diffÃ©rents processus d'initialisation prÃ©alablement initiÃ©s sont achevÃ©s.*/
	get inSync(): Promise<any> {
		if (!this._inSync) {
			this._inSync = this._loadings ? Promise.all(this._loadings) : Promise.resolve();
		}
		return this._inSync;
	}

	/** Configuration du WedModel. */
	setWedModelEndPoint(endPoint: IEndPoint, resolver?: IPathResolver): Promise<any> {
		return this.addAsyncLib(
			WED.loadWedModel(endPoint, resolver).then(wedModel => {this.wedModel = wedModel;})
		);
	}

	/** Configuration du SchÃ©ma RelaxNg. */
	setRelaxNG(endPoint: IEndPoint, init?: RequestInit, skMetaLib?: SkMetaLib): Promise<any> {
		return this.addAsyncLib(
			//TODO regsitre static central des RNG ?
			new RelaxNgSchemaBuilder(skMetaLib).buildRelaxNgFromUrl(endPoint, init).then(rngSchema => {this.schema = rngSchema})
		);
	}

	// async initFromEndPoint(endPoint: IEndPoint, init?: RequestInit, config?: OWedManagerConfig): Promise<boolean> {
	// 	return this.initFromDocument(await endPoint.fetchDom("", init), config);
	// }
	//
	// initFromXml(xml: string, config?: OWedManagerConfig, baseUrl?: IEndPoint): Promise<boolean> {
	// 	return this.initFromDocument(DOM.parseDom(xml, baseUrl), config);
	// }

	// initFromDocument(doc: Document, config?: OWedManagerConfig): Promise<boolean> {
	// 	return this.inSync.then(() => {
	// 		if (!this.schema) throw Error("No Schema declared");
	// 		return this.initFromDocHolder(createDocHolderLocal(this.schema, DOM.cleanupDom(doc, true, false, true), {autoMutate: true, autoComplete: true, autoNormXml: true, autoNormChars: true, genAnnots: true}), config);
	// 	});
	// }

	initFromDocHolder(docHolder: IDocHolder, config: OWedManagerConfig = {}): Promise<boolean> {
		this.rootNode.innerHTML = null;
		return this.inSync.then(() => {
			if (!this.wedModel) throw Error("No wedModel declared");
			if (!config.wedRootModelSelector) config.wedRootModelSelector = WED_SELECTOR_BOX;
			return this.wedMgr.initWedMgr(this.wedModel, docHolder, config);
		});
	}

	onMsgError(errMsg: ErrorMsg): 'redraw' | void {
		if (errMsg.error === 'masterDoorClosed') {
			//FIXME
			alert("Connexion avec le serveur intÃ©rrompue");
			location.reload();
		} else {
			return 'redraw';
		}
	}
}

