/**
 * Layout par dÃ©faut gÃ©rant le positionnement des bars:
 * - commonBar
 * - errorBar
 * - outlineBar
 * - previewBar
 * - metaBar
 */
class FullLayout implements IWedEditorBarLayout {
	editor: IWedEditorMain;

	initLayout(editor: IWedEditorMain, reg: IReg<IUiEnv>): void {
		this.editor = editor;
		//editor.style.overflow = "hidden"; //obligatoire pour que le scroll horizontal fonctionne bien. cf https://stackoverflow.com/a/35609992/8691603
		const sr = editor.shadowRoot;
		reg.installSkin('box-editor/full', sr);
		reg.installSkin('scroll/large', sr);
		editor.rootNode = <div id={editor.config.withoutScrolls ? "rootBox" : "scrollBox"} class="ctn" c-resizable=""/> as HTMLElement & IRegPointer<any>;
		sr.appendChild(<div id="colBox" class="v">
			<div id="rowBox" class="h">
				<div id="metaBox" class="v" c-resizable="">
					{editor.rootNode}
				</div>
				<div id="endBox" class="v" hidden c-resizable=""/>
			</div>
		</div>);
		editor.wedMgr.listeners.on("buildLastDatas", function (this: void, wedMgr: WedMgr, lastDatas: JLastDatas) {
			const sr = wedMgr.wedEditor.shadowRoot;
			const endBox = sr.getElementById('endBox');
			if (!endBox.hidden) {
				lastDatas.lytEndBox = endBox.style.flexBasis;
				lastDatas.lytMetaBox = sr.getElementById('metaBox').style.flexBasis;
				const otl = sr.getElementById('outlinePanel');
				if (otl && !otl.hidden) lastDatas.lytOtl = otl.style.flexBasis;
				const prv = sr.getElementById('previewPanel');
				if (prv && !prv.hidden) lastDatas.lytPrv = prv.style.flexBasis;
			}
		});
		editor.wedMgr.listeners.on("initLastDatas", function (this: void, wedMgr: WedMgr, lastDatas: JLastDatas) {
			const sr = wedMgr.wedEditor.shadowRoot;
			if (lastDatas.lytEndBox) {
				sr.getElementById('endBox').style.flexBasis = lastDatas.lytEndBox;
				if (lastDatas.lytMetaBox) sr.getElementById('metaBox').style.flexBasis = lastDatas.lytMetaBox;
				if (lastDatas.lytOtl) {
					const panel = sr.getElementById('outlinePanel');
					if (panel) panel.style.flexBasis = lastDatas.lytOtl;
				}
				if (lastDatas.lytPrv) {
					const panel = sr.getElementById('previewPanel');
					if (panel) panel.style.flexBasis = lastDatas.lytPrv;
				}
			}
		});
	}

	getBar(id: string): IWedEdBar {
		return this.editor.shadowRoot.getElementById(id);
	}

	showBar(bar: IWedEdBar): boolean {
		let ctn: HTMLElement;
		switch (bar.id) {
		case 'commonBar' :
			ctn = this.editor.shadowRoot.getElementById("colBox");
			if (bar.parentNode !== ctn) ctn.insertBefore(bar, ctn.firstChild);
			break;
		case 'errorBar' :
			ctn = this.editor.shadowRoot.getElementById("rowBox");
			if (bar.parentNode !== ctn) ctn.appendChild(bar);
			break;
		case 'outlineBar' :
			ctn = this.editor.shadowRoot.getElementById("endBox");
			let outlinePanel = DOMSH.findHost(bar) as WedPanelBar;
			if (!outlinePanel) outlinePanel = ctn.insertBefore(<WedPanelBar Ã®={{barBody: bar}} id="outlinePanel" c-resizable=""/>, ctn.firstChild) as WedPanelBar;
			else DOM.setHidden(outlinePanel, false);
			this.refreshEndBox(ctn);
			break;
		case 'previewBar' :
			ctn = this.editor.shadowRoot.getElementById("endBox");
			let previewPanel = DOMSH.findHost(bar) as WedPanelBar;
			if (!previewPanel) previewPanel = ctn.appendChild(<WedPanelBar Ã®={{barBody: bar}} id="previewPanel" c-resizable=""/>) as WedPanelBar;
			else DOM.setHidden(previewPanel, false);
			this.refreshEndBox(ctn);
			break;
		case 'metaBar' :
			ctn = this.editor.shadowRoot.getElementById("metaBox");
			let metaPanel = DOMSH.findHost(bar) as WedPanelBar;
			if (!metaPanel) {
				ctn.appendChild(<c-resizer c-orient="column"/>);
				metaPanel = ctn.appendChild(<WedPanelBar Ã®={{barBody: bar}} id="metaPanel" c-resizable=""/>) as WedPanelBar;
			} else {
				DOM.setHidden(metaPanel, false);
				DOM.setHidden(metaPanel.previousElementSibling, false); //show c-resizer
			}
			this.refreshEndBox(ctn);
			break;
		case 'txtStackBar':
			//1Ã¨re barre en dessous de la zone principale (rowBox)
			const prev = this.editor.shadowRoot.getElementById("rowBox");
			ctn = prev.parentElement;
			if (bar.parentNode !== ctn) ctn.insertBefore(bar, prev ? prev.nextSibling : null);
			break;
		case 'searchBar':
			//barre sous txtStackBar mais au dessus de unknownBar
			const unknownBar = this.editor.shadowRoot.getElementById("unknownBar");
			ctn = this.editor.shadowRoot.getElementById("colBox");
			if (bar.parentNode !== ctn) ctn.insertBefore(bar, unknownBar && unknownBar.parentNode === ctn ? unknownBar : null);
			break;
		case 'diffBar':
			//barre sous commonBar
			const rowBox = this.editor.shadowRoot.getElementById("rowBox");
			ctn = rowBox.parentElement;
			if (bar.parentNode !== ctn) ctn.insertBefore(bar, rowBox);
			break;
		case 'unknownBar':
			//Barre d'alerte d'Ã©lÃ©ments inconnus (associÃ©e Ã  la boxErrBar).
			//DerniÃ¨re barre en dessous de la zone principale
			ctn = this.editor.shadowRoot.getElementById("colBox");
			if (bar.parentNode !== ctn) ctn.appendChild(bar);
			break;
		case 'helpBar' :
			ctn = this.editor.shadowRoot.getElementById("rowBox");
			if (bar.parentNode !== ctn) {
				ctn.appendChild(<c-resizer c-orient="row"/>);
				bar.setAttribute("c-resizable", "");
				ctn.appendChild(bar);
			} else {
				DOM.setHidden(bar.previousElementSibling, false); //show c-resizer
			}
			break;
		default:
			console.warn("Bar unknown by this FullLayout!", this, bar);
			return false;
		}
		VIEWS.onViewShown(bar);
		this.editor.wedMgr.listeners.emit("layoutBarChange", this.editor.wedMgr, bar);
		return true;
	}

	onHideBar(bar: IWedEdBar): void {
		const panel = DOMSH.findHost(bar);
		if (panel instanceof WedPanelBar && DOM.setHidden(panel, true)) {
			const ctn = panel.parentElement;
			if (!ctn) return;
			if (ctn.id === "endBox") this.refreshEndBox(ctn);
			else if (bar.id === "metaBar") DOM.setHidden(panel.previousElementSibling, true); //hide c-resizer
			VIEWS.onViewHidden(bar);
		} else if (bar.id === "helpBar") {
			DOM.setHidden(bar.previousElementSibling, true); //hide c-resizer
		}
		this.editor.wedMgr.listeners.emit("layoutBarChange", this.editor.wedMgr, bar);
	}

	removeBar(bar: IWedEdBar): void {
		if (bar.id === 'unknownBar') {
			if (bar.parentNode) bar.remove();
			return;
		} else if (bar.id === 'helpBar') {
			if (bar.parentNode) {
				bar.previousElementSibling.remove(); //remove c-resizer
				bar.remove();
			}
			return;
		}
		const panel = DOMSH.findHost(bar);
		if (panel instanceof WedPanelBar) {
			const ctn = panel.parentElement;
			if (ctn) {
				if (bar.id === "metaBar") panel.previousElementSibling.remove(); //suppr c-resizer
				panel.remove();
				if (ctn.id === "endBox") this.refreshEndBox(ctn);
				VIEWS.onViewHidden(bar, true);
				this.editor.wedMgr.listeners.emit("layoutBarChange", this.editor.wedMgr, bar);
			}
		}
	}

	/** RÃ©Ã©value l'affichage du endBox et de son resizer et ajuste les resizers dans le endBox. */
	protected refreshEndBox(endBox: HTMLElement) {
		let hidden = true;
		for (let ch = endBox.firstElementChild as HTMLElement; ch; ch = ch.nextElementSibling as HTMLElement) {
			if (!ch.hidden) {
				hidden = false;
				break;
			}
		}
		if (DOM.setHidden(endBox, hidden)) {
			//resizer du endBox
			const prev = endBox.previousElementSibling;
			if (prev) {
				let resizer = prev;
				if (!(resizer instanceof Resizer)) resizer = endBox.parentElement.insertBefore(<c-resizer c-orient="row"/>, endBox);
				DOM.setHidden(resizer, hidden);
			}
		}
		if (!hidden) {
			//adjust c-resizer entre les bars dans le endBox.
			let ch = endBox.firstElementChild;
			let prevVisible = false;
			while (ch) {
				if (!(ch as HTMLElement).hidden) prevVisible = true;
				const next = ch.nextElementSibling;
				if (next instanceof Resizer) {
					ch = next.nextElementSibling;
					DOM.setHidden(next, !prevVisible || (ch as HTMLElement).hidden);
					prevVisible = false;
					continue;
				} else if (next) {
					const resizer = endBox.insertBefore(<c-resizer c-orient="column"/>, next);
					if (!prevVisible || (next as HTMLElement).hidden) resizer.hidden = true;
				}
				prevVisible = false;
				ch = next;
			}
		}
	}

}


